.Layout{
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 300px;
    height: 100%;
    margin-top: 0;
    padding-top: 10%;
    background-color: white;
    font-size: 24px;
    border: 3px solid yellow;
    left: -400px;
    z-index: 100;
    transition: all 0.3s;
}

.Layout a{
    text-decoration: none;
    color: black;
    margin-top: 10px;
}

.openMenu{
    position: absolute;
    z-index: 99;
    left: 10px;
    top: 10px;
}

.show{
    left: 0;
}


