.Layout{
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 35%;
    text-align: center;
    margin-top: 0;
    padding-top: 10%;
    background-color: white;
    font-size: 24px;
    border: 3px solid yellow;
    left: -500px;
    z-index: 100;
    transition: all 0.3s;
}

.Layout a{
    text-decoration: none;
    color: black;
    margin: 10px;
}

.closeMenu{
    position: absolute;
    top: 10px;
    right: 10px;
}

.show{
    left: 0;
}