.Layout{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    width: 300px;
    height: 100%;
    margin-top: 0;
    padding-top: 10%;
    background-color: white;
    font-size: 24px;
    border: 3px solid yellow;
    left: -400px;
    z-index: 100;
    transition: all 0.3s;
}

.Layout a{
    text-decoration: none;
    color: black;
    margin-top: 10px;
}

.openMenu{
    position: absolute;
    z-index: 99;
    left: 10px;
    top: 10px;
}

.show{
    left: 0;
}



.Layout{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    width: 35%;
    text-align: center;
    margin-top: 0;
    padding-top: 10%;
    background-color: white;
    font-size: 24px;
    border: 3px solid yellow;
    left: -500px;
    z-index: 100;
    transition: all 0.3s;
}

.Layout a{
    text-decoration: none;
    color: black;
    margin: 10px;
}

.closeMenu{
    position: absolute;
    top: 10px;
    right: 10px;
}

.show{
    left: 0;
}
.App{
    width: 100%;
    min-height: 600px;
    margin: 0 auto;
    padding-top: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(97, 114, 243);
    color: black;
    font-size: 24px;
}

h1, h2, h3{
    color: white;
}


